import React from "react";
import './Profile.css'
import ElectronicCard from "../../components/ElectronicCard/ElectronicCard";
import lab from '../../img/electronic_card_img/lab.svg'
import clock from '../../img/electronic_card_img/clock.svg'
import add from '../../img/electronic_card_img/add.svg'
import report from '../../img/electronic_card_img/report.svg'
import Appointment from "../../components/Appointment/Appointment";
import {useNavigate} from "react-router-dom";

 const Profile = props => {

    const navigate = useNavigate();
    let appointment
    if (props.appointment.length > 0) {
      appointment = props.appointment.map((name, index) => {
        if (index < 2 && index > -1) {
          return (
              <Appointment
                  key={index}
                  day={name.day}
                  data={name.data}
                  time={name.time}
                  name={name.name}
                  address={name.address}
                  post={name.post}
                  photo={name.photo}
                  onDelete={props.onDelete}
              />
          )
          }else{
          return null
        }
      })
    } else {
      appointment = <div style={{fontSize: 25, margin: 25}}> </div>
    }

    return (
        <div className={'Profile'}>
          <div style={{marginBottom: 35}}>
            <p className={'title'}>  </p>
            <div className={'card_list'}>
              {appointment}
              <div className={'card_list_more'}>
                <p style={{margin: 0, padding: 0}}> &nbsp;

                  {(props.appointment.length - 2) > 0 ? props.appointment.length - 2 : 0} &nbsp;
                  {(props.appointment.length - 2 === 1) ? ''
                      : props.appointment.length - 2 < 1 ? '' : ''
                  }

                </p>
                <span onClick={() => navigate('/profile/detail')} className={'navigation'}>  </span>
              </div>
            </div>
          </div>
          <div>
            <p className={'title'}> </p>
            <div className={'cards'}>
              <ElectronicCard
                  url={report}
                  title={'  '}
                  children={
                    <ul>
                      <li><span>  </span></li>
                      <li><span> </span></li>
                      <li><span> </span></li>
                    </ul>
                  }
              />


              <ElectronicCard
                  url={lab}
                  title={' '}
                  children={<p style={{width: 324}}>      </p>}
              />
            </div>

            <div className={'cards'}>
              <ElectronicCard
                  url={add}
                  title={'  '}
                  children={<p style={{width: 324}}>       </p>}
              />

              <ElectronicCard
                  url={clock}
                  title={' '}
                  children={<p style={{width: 324}}>         </p>}
              />
            </div>
          </div>
        </div>


    )
}
export default Profile